%pageelement {
    display: flex;
    justify-content: center;
    position: absolute;
    width: 100%; }

.intro {
    @extend %pageelement;
    overflow: hidden;
    height: 100%;
    z-index: 0; }

.main {
    @extend %pageelement;
    display: none;
    background-color: $PRIMARYCOLOR;
    width: 100%;
    height: 100%;
    z-index: 1; }

.settings {
    @extend %pageelement;
    display: none;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    min-width: 400px;
    z-index: 3; }

.table {
    @extend %pageelement;
    display: none;
    align-items: center;
    background-color: $GREY;
    width: 100%;
    height: 100%;
    min-width: 29.7cm;
    min-height: 21cm;
    z-index: 4; }
